const USERKEY = 'userkey';
//removeItem
export default {
    saveStaff( staff ){
        sessionStorage.setItem(USERKEY, JSON.stringify(staff));
    },
    getStaff(){
        return JSON.parse(sessionStorage.getItem(USERKEY)) || {};
    },
    removeStaff(){
        sessionStorage.removeItem(USERKEY);
    }
}
