import React from 'react';
import './index.less';
import { Form, Icon, Input, Button, message } from 'antd';
import { reqLogin } from '../../api';
import { Redirect } from 'react-router-dom';
import storageUtils from '../../utils/storageUtils';
import sessionUtils from '../../utils/sessionUtils';

const Login = props => {
    let handleSubmit = e => {
        e.preventDefault();
        props.form.validateFields(async (err, values) => {
            const { username, password } = values;
            if (!err) {
                let user = await reqLogin(username, password);
                if (user.status === 0) {
                    //localstorage, web
                    storageUtils.saveStaff(user.data);
                    //sessionweb
                    sessionUtils.saveStaff(user.data);
                    props.history.replace('/');
                } else {
                    error(user.msg, 2);
                }
            }
        });
    };

    //
    const error = (errMsg, duration) => {
        message.error(errMsg, duration);
    };
    //validator
    let validatePassword = async (rule, value, callback) => {
        try {
            if (!value) {
                throw new Error('');
            } else if (!/^[\w@]{4,10}$/.test(value)) {
                throw new Error('_@,4-10)');
            }
        } catch (err) {
            callback(err);
        }
    };

    const { getFieldDecorator } = props.form;
    if (sessionUtils.getStaff().staff) {
        return <Redirect to="/" />;
    } else {
        return (
            <div className="login clearmargin">
                <div className="mallmanasys-info">
                    <h1></h1>
                    <p className="mallmanasys-alias">Second-hand mall management system</p>
                    <p className="mallmanasys-author"> </p>
                </div>
                <Form onSubmit={handleSubmit} className="login-form">
                    <Form.Item>
                        {getFieldDecorator('username', {
                            rules: [
                                {
                                    required: true,
                                    message: ''
                                },
                                {
                                    pattern: /^[\u4E00-\u9FA5\w@]+$/,
                                    message: '_@'
                                }
                            ],
                            initialValue: 'root'
                        })(
                            <Input
                                prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                placeholder="Username"
                            />
                        )}
                    </Form.Item>
                    <Form.Item>
                        {getFieldDecorator('password', {
                            rules: [
                                {
                                    validator: validatePassword
                                }
                            ],
                            initialValue: 'root'
                        })(
                            <Input.Password
                                placeholder="input password"
                                prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            />
                        )}
                    </Form.Item>
                    <Form.Item className="login-btn">
                        <Button type="primary" htmlType="submit" className="login-form-button">
                            
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        );
    }
};
const WrappedNormalLoginForm = Form.create({ name: 'normal_login' })(Login);
export default WrappedNormalLoginForm;
