import React, { Component } from 'react';
import { Card, Button, Table, Modal, message } from 'antd';
import { PAGE_SIZE } from '../../utils/constants';
import { reqRoles, reqAddRole, reqUpdateRole, reqDeleteRole } from '../../api';
import AddForm from './addform';
import AuthForm from './authform';
import sessionUtils from '../../utils/sessionUtils';
import formatDate from '../../utils/dateUtils';
import storageUtils from '../../utils/storageUtils';
import modal from '../../utils/modal';
import pagination from '../../utils/pagination';

export default class Role extends Component {
    state = {
        roles: [], // 
        role: {}, // role
        isShowAdd: false, // 
        isShowAuth: false, // 
        currentPage: 1 //
    };

    constructor(props) {
        super(props);

        this.auth = React.createRef();
    }

    initColumn = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'roleName',
                align: 'center'
            },
            {
                title: '',
                dataIndex: 'createTime',
                align: 'center'
            },
            {
                title: '',
                dataIndex: 'authTime',
                align: 'center',
                render: formatDate
            },
            {
                title: '',
                dataIndex: 'authName',
                align: 'center'
            }
        ];
    };

    getRoles = async () => {
        const result = await reqRoles();
        if (result.status === 0) {
            const roles = result.data;
            this.setState({
                roles
            });
        }
    };
    //
    onRow = role => {
        return {
            onClick: event => {
                // 
                if (role.roleName === '') {
                    this.setState({
                        role: {}
                    });
                } else {
                    this.setState({
                        role
                    });
                }
            }
        };
    };

    /*
  /
   */
    addRole = () => {
        this.form.validateFields(async (error, values) => {
            if (!error) {
                // 
                const { roleName } = values;
                let authName = sessionUtils.getStaff().staff.username;
                this.setState({ isShowAdd: false });
                this.form.resetFields();
                //
                if (this.isUpdateRoleName) {
                    const { role, roles } = this.state;
                    role.roleName = roleName;
                    const result = await reqUpdateRole(role);
                    if (result.status === 0) {
                        for (let roleItem of roles) {
                            if (roleItem.roleId === role.roleId) {
                                roleItem.roleName = roleName;
                            }
                        }
                        this.setState(state => ({
                            roles: [...state.roles]
                        }));
                        this.isUpdateRoleName = false;
                    }
                } else {
                    //
                    let createTime = formatDate(Date.now());
                    const result = await reqAddRole({ roleName, createTime, authName, authTime: createTime });
                    // /
                    if (result.status === 0) {
                        const role = result.data;
                        // roles: 
                        let currentPage = pagination.showCurrentPage(this.state.roles.length + 1, PAGE_SIZE);
                        this.setState(state => ({
                            roles: [...state.roles, role],
                            role: { ...role },
                            currentPage
                        }));
                    } else {
                        message.error(': ' + result.msg);
                    }
                }
            } else {
                message.error(`${this.isUpdateRoleName ? '' : ''}`);
            }
        });
    };

    /*
      
       */
    updateRole = async () => {
        // 
        this.setState({
            isShowAuth: false
        });
        const { role, roles } = this.state;
        // menus
        const menus = this.auth.current.getMenus();
        role.menus = menus.join('#');
        role.authTime = formatDate(Date.now());
        role.authName = sessionUtils.getStaff().staff.username;

        // 
        const result = await reqUpdateRole(role);
        if (result.status === 0) {
            // this.getRoles()
            // , 
            if (role.roleId === sessionUtils.getStaff().staff.roleId) {
                sessionUtils.removeUser();
                storageUtils.removeUser();
                this.props.history.replace('/login');
                message.success('');
            } else {
                message.success('');
                let roleIndex = roles.findIndex(item => item.roleId === role.roleId);
                roles[roleIndex] = role;
                this.setState({
                    roles: [...roles],
                    role: { ...role }
                });
            }
        }
    };
    //
    deleteRole = () => {
        const { role, roles } = this.state;
        let title = `${role.roleName}?`;
        modal.confirmModel(title, async () => {
            let result;
            result = await reqDeleteRole(role.roleId);
            if (result.status === 0) {
                for (let i = 0; i < roles.length; i++) {
                    if (roles[i].roleId === role.roleId) {
                        roles.splice(i, 1);
                    }
                }
                this.setState(state => ({
                    roles: [...state.roles],
                    role: {}
                }));
            }
        });
    };
    //
    paginationChange = page => {
        this.setState({
            currentPage: page,
            role: {}
        });
    };
    //
    updateRoleName = () => {
        this.setState({
            isShowAdd: true
        });
        this.isUpdateRoleName = true;
    };
    componentWillMount() {
        this.initColumn();
    }
    //
    componentDidMount() {
        this.getRoles();
    }

    render() {
        const { roles, role, isShowAdd, isShowAuth, currentPage } = this.state;
        const modalTitle = !this.isUpdateRoleName ? '' : '';
        const title = (
            <span>
                <Button type="primary" onClick={() => this.setState({ isShowAdd: true })}>
                    
                </Button>{' '}
                &nbsp;&nbsp;
                <Button type="primary" disabled={!role.roleId} onClick={() => this.setState({ isShowAuth: true })}>
                    
                </Button>
                &nbsp;&nbsp;
                <Button type="primary" disabled={!role.roleId} onClick={this.updateRoleName}>
                    
                </Button>
                &nbsp;&nbsp;
                <Button type="danger" disabled={!role.roleId} onClick={this.deleteRole}>
                    
                </Button>
            </span>
        );

        return (
            <Card title={title} >
                <Table
                    bordered
                    rowKey="roleId"
                    dataSource={roles}
                    columns={this.columns}
                    pagination={{
                        size: 'small',
                        defaultPageSize: PAGE_SIZE,
                        showQuickJumper: true,
                        showSizeChanger: true,
                        current: currentPage,
                        pageSizeOptions: ['' + PAGE_SIZE, '' + PAGE_SIZE * 2, '' + PAGE_SIZE * 3, '' + PAGE_SIZE * 4],
                        showTotal: pagination.showTotal,
                        onChange: this.paginationChange
                    }}
                    rowSelection={{
                        type: 'radio',
                        selectedRowKeys: [role.roleId],
                        getCheckboxProps: record => ({
                            disabled: record.roleName === '',
                            name: record.roleName
                        }),
                        onSelect: role => {
                            // radio
                            this.setState({
                                role
                            });
                        }
                    }}
                    onRow={this.onRow}
                />

                <Modal
                    title={modalTitle}
                    visible={isShowAdd}
                    onOk={this.addRole}
                    onCancel={() => {
                        this.setState({ isShowAdd: false });
                        this.form.resetFields();
                        this.isUpdateRoleName = false;
                    }}
                >
                    <AddForm
                        setForm={form => (this.form = form)}
                        roleName={role.roleName}
                        isUpdateRoleName={this.isUpdateRoleName}
                    />
                </Modal>

                <Modal
                    title=""
                    visible={isShowAuth}
                    onOk={this.updateRole}
                    onCancel={() => {
                        this.setState({ isShowAuth: false });
                    }}
                >
                    <AuthForm ref={this.auth} role={role} />
                </Modal>
            </Card>
        );
    }
}
