const USERKEY = 'userkey';
//removeItem
export default {
    saveStaff(staff){
        localStorage.setItem(USERKEY, JSON.stringify(staff));
    },
    getStaff( ){
        return JSON.parse(localStorage.getItem(USERKEY)) || {};
    },
    removeStaff(){
        localStorage.removeItem(USERKEY);
    }
}
