import 'braft-editor/dist/index.css';
import 'braft-extensions/dist/color-picker.css';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { message } from 'antd';
import { reqDeleteFile } from '../../api';
import BraftEditor from 'braft-editor';
import ColorPicker from 'braft-extensions/dist/color-picker';

BraftEditor.use(
    ColorPicker({
        includeEditors: ['editor-with-color-picker'],
        theme: 'light' // darklightdark
    })
);

export default class RichText extends Component {
    constructor(props) {
        super(props);

        const { productDetail } = this.props;
        if (productDetail) {
            let editorState = BraftEditor.createEditorState(productDetail);
            this.state = {
                editorState
            };
        } else {
            this.state = {
                editorState: BraftEditor.createEditorState() //
            };
        }
    }
    //
    preview = () => {
        if (window.previewWindow) {
            window.previewWindow.close();
        }

        window.previewWindow = window.open();
        window.previewWindow.document.write(this.buildPreviewHtml());
        window.previewWindow.document.close();
    };

    buildPreviewHtml() {
        return `
          <!Doctype html>
          <html>
            <head>
              <title>Preview Content</title>
              <style>
                html,body{
                  height: 100%;
                  margin: 0;
                  padding: 0;
                  overflow: auto;
                  background-color: #f1f2f3;
                }
                .container{
                  box-sizing: border-box;
                  width: 1000px;
                  max-width: 100%;
                  min-height: 100%;
                  margin: 0 auto;
                  padding: 30px 20px;
                  overflow: hidden;
                  background-color: #fff;
                  border-right: solid 1px #eee;
                  border-left: solid 1px #eee;
                }
                .container img,
                .container audio,
                .container video{
                  max-width: 100%;
                  height: auto;
                }
                .container p{
                  white-space: pre-wrap;
                  min-height: 1em;
                }
                .container pre{
                  padding: 15px;
                  background-color: #f1f1f1;
                  border-radius: 5px;
                }
                .container blockquote{
                  margin: 0;
                  padding: 15px;
                  background-color: #f1f1f1;
                  border-left: 3px solid #d1d1d1;
                }
              </style>
            </head>
            <body>
              <div class="container">${this.state.editorState.toHTML()}</div>
            </body>
          </html>
        `;
    }

    contentChange = editorState => {
        this.setState({
            editorState: editorState
        });
    };
    uploadMedia = media => {
        const { file } = media;
        let regImg = /image\/\*/;
        let serverUrl;
        let filemime = file.type;
        if (regImg.test(filemime)) {
            serverUrl = '/product/img/upload';
        } else {
            serverUrl = '/product/vedio/upload';
        }
        //xhr
        const xhr = new XMLHttpRequest();
        //
        const fd = new FormData();

        const successFn = res => {
            const response = JSON.parse(xhr.responseText);
            let uploadResData;
            if (regImg.test(filemime)) {
                uploadResData = Object.assign(response.data);
            } else {
                uploadResData = Object.assign(response.data);
            }
            media.success({
                url: uploadResData.url,
                name: uploadResData.fileName,
                meta: {
                    controls: true, // 
                    poster: uploadResData.poster
                }
            });
        };
        const progressFn = event => {
            // param.progress
            media.progress((event.loaded / event.total) * 100);
        };
        const errorFn = response => {
            // param.error
            media.error({
                msg: 'unable to upload.'
            });
        };
        xhr.upload.addEventListener('progress', progressFn, false);
        //
        xhr.addEventListener('load', successFn, false);
        //
        xhr.addEventListener('err', errorFn, false);
        //
        fd.append('image', media.file);
        // type url
        xhr.open('POST', serverUrl, true);
        //
        xhr.send(fd);
    };
    //30M
    myValidateFn = file => {
        return file.size < 1024 * 40000;
    };
    //
    hooksConfig = {
        'remove-medias': async file => {
            let result = 0;
            for (let fileObj of file) {
                let fileName = fileObj.name;
                result = await reqDeleteFile({ fileName });
            }
            if (result.status === 0) {
                message.success(result.msg);
            }
        }
    };
    //
    getProductDetail = () => {
        // html
        return this.state.editorState.toHTML();
    };

    render() {
        const { editorState } = this.state;
        const extendControls = [
            {
                key: 'custom-button',
                type: 'button',
                text: '',
                onClick: this.preview
            }
        ];
        const excludeControls = ['code'];
        return (
            <div>
                <div className="editor-wrapper">
                    <BraftEditor
                        style={{ border: '1px solid #d9d9d9' }}
                        contentStyle={{ height: '300px' }}
                        placeholder=""
                        value={editorState}
                        onChange={this.contentChange}
                        excludeControls={excludeControls}
                        extendControls={extendControls}
                        media={{ uploadFn: this.uploadMedia, validateFn: this.myValidateFn }}
                        hooks={this.hooksConfig}
                        id="editor-with-color-picker"
                    />
                </div>
            </div>
        );
    }
}
RichText.propTypes = {
    productDetail: PropTypes.string
};
