import React from 'react'
import { Modal } from 'antd';
const { confirm, info } = Modal;

//
const showUnSelectRowinfo = () => {
    info({
        title: '',
        content: <div></div>,
        okText: '',
        width: 330
    });
};
//
const confirmModel = (
    title,
    callback,
    okType = 'primary',
    okText = '',
    cancelText = '',
    width = 260
) => {
    confirm({
        title: title,
        okText: okText,
        okType: okType,
        cancelText: cancelText,
        className: 'logoutconfirm',
        width: width,
        onOk() {
            callback();
        }
    });
};
export default {
    showUnSelectRowinfo,
    confirmModel
};
